/* include the font and colors chosen for the project */
@import url("https://fonts.googleapis.com/css?family=Open+Sans|Overpass+Mono");

:root {
  --font: 'Open Sans', sans-serif;
  --clr-theme: #9206f5;
  --clr-theme-drk: #6604ac;
  --clr-theme-lgh: #9d1bf9;
  --clr-phone: #fff;
  --clr-text: #515151;
  --clr-text-transparent: #5151514d;
}

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  width: 100%;
  min-height: 100vh;
  /* include the chosen variables for the color of text, backgroun and the font family */
  color: var(--clr-text);
  background: -webkit-gradient(linear, right bottom, left top, from(var(--clr-theme)), to(var(--clr-theme-lgh)));
  background: -webkit-linear-gradient(bottom right, var(--clr-theme), var(--clr-theme-lgh));
  background: -o-linear-gradient(bottom right, var(--clr-theme), var(--clr-theme-lgh));
  background: linear-gradient(to top left, var(--clr-theme), var(--clr-theme-lgh));
  font-family: var(--font);
  /* center the phone UI horizontally  */
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  margin: 2.5rem 0;
}


