.CalculatorDisplay {
  /* 
  position the different displays one on top of the other
  push them both at the bottom of the container
  */
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: end;
      justify-content: flex-end;
  /* align the text to the right */
  text-align: right;
  padding: 1rem;
  /* border bottom to separate the displays from the buttons */
  border-bottom: 1px solid var(--clr-text-transparent);
  font-family: var(--font);
}

/* the headers are styled through the styled-components library */


